.profile__about {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.22;
  margin: 0;
  max-width: 100%;
}

@media screen and (max-width: 918px) {
  .profile__about {
    font-size: 16px;
  }
}

@media screen and (max-width: 680px) {
  .profile__about {
    font-size: 14px;
    line-height: 1.21;
    text-align: center;
  }
}

