.profile {
  max-width: 880px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 40px 0 50px;
}

@media screen and (max-width: 918px) {
  .profile {
    padding-right: 19px;
    padding-left: 19px;
  }
}

@media screen and (max-width: 680px) {
  .profile {
    flex-direction: column;
    padding-top: 42px;
    padding-bottom: 36px;
  }
}
